	module.exports = {"data":{"post":{"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2006/11/perfect_family_christmas.jpg\" alt=\"perfect family Christmas\" /></p>\n<p>As the first of December approaches and the first Sunday in Advent, I thought we would share this envelope we received in the mail that pictures the perfect family Christmas... in our worst nightmare! If these children show up at your house, have the parents arrested for child abuse. That girl especially... she's either the <a href=\"http://www.wonkette.com/politics/capitol-hill/congressional-catfight-harris-vs-pelosi-175045.php\">young Nancy Pelosi</a> or the <a href=\"http://www.wonkette.com/politics/halloween/chilling-cocktobers-house-of-representatives-211355.php\">young Laura Bush</a>.</p>","fields":{"title":"Perfect Family Christmas","date":"30 Nov 2006","url":"https://papascott.de/2006-11-30-perfect-family-christmas/"}}},"pathContext":{"slug":"/2006-11-30-perfect-family-christmas/","prev":{"fields":{"title":"Are Eggs Liquids?","date":"30 Nov 2006","slug":"/2006-11-30-are-eggs-liquids/"}},"next":{"fields":{"title":"Should Americans Be Allowed to Marry?","date":"30 Nov 2006","slug":"/2006-11-30-should-americans-be-allowed-to-marry/"}}}}

